$lightgray: #f1f4f7;

.course {
  margin: 20px 0;
  padding-bottom: 50px;

  p {
    font-size: 20px;
    font-weight: 300;
    margin: 15px 0;
    line-height: 24px;

    + .course__element {
      margin-top: 0;
    }

  }

  .course__element {
    background-color: $lightgray;
    padding: 25px 70px;
    box-sizing: border-box;
    margin: 30px 0;
    min-height: 500px;
  }

  .course__img {
    width: 230px;
    text-align: center;
    float: left;

    img {
      display: block;
      max-width: 100%;
      margin-top: 30px;
    }

  }

  .course__txt {
    width: calc(100% - 300px);
    float: right;
  }

  .course__title {
    margin: 0 0 5px 0;
    font-family: Montserrat;
    font-weight: 800;
    line-height: normal;
    font-size: 20px;
  }

  .course__author {
    color: #7a8694;
    font-size: 14px;
    font-weight: 300;
    margin: 5px 0;
  }

  .course__icons {
    margin: 20px 0 15px;

    p {
      font-size: 14px;
      line-height: 26px;
      font-weight: 300;
      margin: 0;
      padding: 5px 0;
    }

    .course__lesson {
      background: url('../img/lesson.png') left center no-repeat;
      padding-left: 40px;
      margin-right: 40px;
    }

    .course__time {
      background: url('../img/time.png') left center no-repeat;
      padding-left: 30px;
    }

  }

  .course__excerpt {
    color: $dark-gray;
    font-size: 16px;
    line-height: 24px;

    span {
      font-size: 14px;
      font-style: italic;
      display: block;
      line-height: 20px;
    }

  }

  .course__buttons {
    margin: 20px 0;
  }

  .course__input {
    width: 213px;
    margin: 15px 0;

    input {
      width: 100%;
      border: 1px solid $blue;
      font-size: 14px;
      font-weight: 300;
      color: $dark-gray;
      line-height: 16px;
      padding: 7px 0 7px 40px;
      box-sizing: border-box;
    }

    ::-webkit-input-placeholder {
      color: $dark-gray;
    }

    :-moz-placeholder {
      color: $dark-gray;
    }

    ::-moz-placeholder {
      color: $dark-gray;
    }

    :-ms-input-placeholder {
      color: $dark-gray;
    }

  }


  .course__input--person {

    input {
      background: url('../img/person.png') 12px 8px no-repeat;
    }

  }

  .course__input--email {

    input {
      background: url('../img/mail.png') 12px 7px no-repeat;
    }

  }

}

.course--two-block {
  overflow: hidden;

  .course__element {
    display: inline-block;
    box-shadow: 0 0 10px rgba(0,0,0,.1);
    background-color: $white;
    width: 49%;
    padding: 23px 20px;
    position: relative;
    margin: 12px 0;

    &:nth-child(even) {
      float: right;
    }

  }

  .course__title {
    font-size: 20px;
    margin: 0;
  }

  .course__author {
    margin-bottom: 0;
  }

  .course__free {
    position: absolute;
    right: 0;
    top: 0;
    font-size: 14px;
    line-height: 26px;
    background-color: #8dc63f;
    padding: 0 10px;
    color: $white;
    font-weight: 700;
    margin: 0;
  }

  .course__buttons {
  }

  .course__txt {
    width: calc(100% - 265px);
  }

  .course__icons {
    margin: 0;

    .course__lesson {
      margin-right: 0;
    }

    .course__time {
      padding-left: 40px;
      background-position: 4px center;
    }

  }

  .course__excerpt {
    font-size: 14px;
    line-height: 21px;
    margin: 10px 0;
    color: #586377;
  }

  .course__buttons {
    margin: 0;

    a {
      margin: 5px 0;
    }

  }

  .course__price {
    font-size: 16px;
    line-height: 26px;
    font-weight: 700;
    color: $dark-gray;

    span {
      font-size: 20px;
      color: $black;
      margin-left: 5px;
    }

  }

}

@media screen and (max-width: 1124px) {

  .course {

    .course__element {
      padding: 25px;
    }

    .course__buttons {

      a {

      }

    }

  }

  .course--two-block {

    .course__element {
      padding: 23px 15px;
    }

    .course__buttons {

      a {

      }

    }

  }

}

@media screen and (max-width: 1124px) {

  .course--two-block {

    .course__element {
      width: 100%;
      min-height: auto;
    }

    .course__buttons {

      a {
        margin: 10px 15px 10px 0;

        &:first-child {
          margin: 10px 15px 10px 0;
        }

        &:last-child {
          margin: 10px 0;
        }

      }

    }

    .course__author {
      margin: 5px 0 10px;
    }

    .course__input {
      margin: 15px 0;

      &:last-child {
        margin-bottom: 5px;
      }

    }

  }

}

@media (max-width: 560px) {

  .course {

    .course__img {
      float: none;
      margin: 0 auto;
    }

    .course__txt {
      width: 100%;
      float: none;
    }

  }

}