.section {
  margin: 30px 0 60px;
  background: #2A59FF;
  padding-top: 35px;
  position: relative;

  .section__img {
    display: block;
    margin: 0 auto;
    max-width: 100%;
    height: auto;
  }

  .section__name {
    font-weight: 700;
    line-height: 41px;
    font-size: 30px;
    text-align: center;
    color: $white;
    margin: 10px 0 15px;
    padding-left: 10px;
    padding-right: 10px;

    &::before {
      content: 'Раздел 1.';
      counter-increment: item;
      font-weight: 700;
      font-size: 18px;
      line-height: 26px;
      display: block;
      width: 120px;
      border: 2px solid #FFD63F;
      padding-top: 3px;
      padding-bottom: 3px;
      color: $white;
      margin: 0 auto 10px;
    }

  }

  .section__show {
    text-align: center;
    margin: 0;
    position: absolute;
    left: 50%;
    bottom: -21px;
    transform: translateX(-50%);

    span, img {
      cursor: pointer;
    }

    .rotate {
      transform: rotate(180deg);
    }

    a {
      color: $white;
      text-decoration: none;
      
      &:hover {
        text-decoration: underline;
      }
      
    }


  }

  .section__lessons {
    background-color: #F8F9FB;
    padding-top: 30px;
    padding-bottom: 30px;
    display: none;
  }

  .section__lesson {
    margin: 15px 0;
    @include clearfix;

    div {
      font-size: 20px;
      line-height: 28px;
      color: #2A59FF;
      margin-left: 169px;
      float: left;
      
      a {
        color: $black;
        text-decoration: underline;
        display: inline-block;
        line-height: 28px;
        font-size: 20px;
        margin-left: 5px;
        
        &:hover {
          text-decoration: none;
        }
        
      }

    }

  }

  div.section__homework-block {
    position: relative;
    margin-left: 20px;

    .practice {
      background: url("../img/practice.png") left 3px no-repeat;
      color: #498bff;
      padding-left: 25px;
      -webkit-box-sizing: border-box;
      box-sizing: border-box;
      margin: 0 15px!important;
      line-height: 21px;
      font-size: 14px;
      text-decoration: underline;
    }

    .section__homework {
      background-color: #d7d7d7;
      border-radius: 10px;
      padding: 20px 25px;
      width: 420px;
      -webkit-box-sizing: border-box;
      box-sizing: border-box;
      position: absolute;
      margin-left: 0;
      right: -90px;
      top: 25px;
      display: none;
      z-index: 10;

      &:before {
        content: "";
        border: 18px solid transparent;
        border-bottom: 30px solid #D7D7D7;
        border-top-width: 1px;
        position: absolute;
        top: -27px;
        left: 50%;
        transform: translate(-50%, 0);
        z-index: 8;
      }

    }

  }

}

.section--bonus {
  padding-top: 0;
}

.section__block {
  display: table;
  width: 100%;
  padding-top: 30px;
  padding-bottom: 30px;
  background: url("../img/bonus-bg.png") center bottom no-repeat;
}

.section__left {
  display: table-cell;
  width: 40%;
  padding-left: 50px;
}

.section__right {
  display: table-cell;
  width: 60%;
  vertical-align: middle;
}

.section__homework {
  margin-left: 145px;
  margin-top: 30px;
  position: relative;

  p {
    font-size: 16px;
    line-height: 140%;
    margin: 5px 0;
    color: $black;
  }

  .practice {
    border-radius: 5px;
    text-decoration: none;
    color: $white;
    width: 160px;
    line-height: 40px;
    text-align: center;
    box-sizing: border-box;
    background-color: #14D1BA;
    font-weight: 700;
    margin-left: 10px;

    span {
      background: url("../img/icon-homework.svg") left center no-repeat;
      padding-left: 25px;
    }

    &:hover {
      background-color: darken(#14D1BA, 10%);
    }

  }

  .section__exercise {
    background-color: #D7D7D7;
    border-radius: 10px;
    padding: 20px 25px;
    width: 420px;
    box-sizing: border-box;
    position: absolute;
    margin-left: 0;
    left: 0;
    top: 60px;
    display: none;
    z-index: 10;

    &:before {
      content: "";
      border: 18px solid transparent;
      border-bottom: 30px solid #D7D7D7;
      border-top-width: 1px;
      position: absolute;
      top: -27px;
      left: 50%;
      transform: translate(-50%, 0);
      z-index: 8;
    }

    p {
      margin: 10px 0;
      color: $black;
      font-size: 16px;
      line-height: 20px;
    }

    a {
      font-size: 16px;
      line-height: 20px;
      color: $fourtly;
      text-decoration: underline;
      margin-left: 0;

      &:hover {
        text-decoration: none;
      }

    }
  }

}

.section__materials {
  margin: 50px 0 70px;
  overflow: hidden;
}

.section__material {
  float: left;
  margin-right: 20px;
  margin-bottom: 10px;
  margin-top: 10px;

  a {
    width: 220px;
    line-height: 50px;
    background-color: #2A59FF;
    border-radius: 5px;
    font-size: 18px;
    color: $white;
    display: block;
    text-decoration: none;
    font-weight: 700;
    text-align: center;

    &:hover {
      background-color: darken(#2A59FF, 10%);
    }

  }

  &:nth-child(1) {

    span {
      padding-left: 30px;
      background: url("../img/icon1.svg") left center no-repeat;
    }

  }

  &:nth-child(2) {

    span {
      padding-left: 30px;
      background: url("../img/icon-two.png") left center no-repeat;
    }

  }

  &:nth-child(3) {

    span {
      padding-left: 30px;
      background: url("../img/icon3.svg") left center no-repeat;
    }

  }

  &:nth-child(4) {
    margin-right: 0;

    span {
      padding-left: 30px;
      background: url("../img/icon4.svg") left center no-repeat;
    }

  }

}

.section {

  @for $i from 1 through 10 {
    &:nth-of-type(#{$i}) {

      .section__name {

          &::before {
            content: 'Раздел #{$i}';
          }

      }

    }

  }

}

@media (max-width: 980px) {

  .section {

    .section__lesson {
      position: relative;

      div {
        font-size: 16px;
        line-height: 20px;

        a {
          font-size: 16px;
          line-height: 20px;
        }

      }

    }

    div.section__homework-block {
      position: static;

      .section__homework {
        width: 290px;
        padding: 10px;
        right: auto;
        left: 50%;
        transform: translateX(-50%);

        p {
          font-size: 14px;
        }

      }

    }

    .section__material {
      width: 50%;
      margin-right: 0;

      a {
        margin: 0 auto;
        width: 210px;
      }

    }

  }

}

@media (max-width: 880px) {

  .section {

    .section__lesson {

      div {
        margin-left: 25px;
      }

    }

  }

}

@media (max-width: 680px) {

  .section {

    .section__name {
      font-size: 22px;
      line-height: 26px;
    }

  }

}

@media (max-width: 560px) {

  .section__left, .section__right {
    width: 100%;
    display: block;
    padding-left: 0;
  }

}

@media (max-width: 480px) {

  .section {

    .section__material {
      width: 100%;
    }

  }

}