.header {
  background: linear-gradient(179.42deg, #651FFF 12.94%, #2979FF 108.88%);
}

.header__wrap {
  padding-top: 1px;
  padding-bottom: 1px;
  position: relative;
  padding-left: 600px;
}

.header__img {
  position: absolute;
  left: 60px;
  bottom: 5px;
}

.header__course {
  text-align: left;
  padding-top: 40px;
}

.header__author {
  font-size: 18px;
  line-height: 25px;
  color: $white;
  margin: 0 0 30px 0;
}

.header__videocourse {
  width: 195px;
  line-height: 25px;
  padding-top: 6px;
  padding-bottom: 5px;
  font-size: 18px;
  letter-spacing: 0.25em;
  text-align: center;
  margin: 20px 0;
  color: $white;
  border: 2px solid #FFD63F;
  text-transform: uppercase;
  font-weight: 800;
}

.header__name {
  font-family: Montserrat;
  font-weight: 800;
  line-height: 49px;
  font-size: 40px;
  color: $white;
  margin: 20px 0;

  span {
    color: #FFD63F;
    font-size: 46px;
    line-height: 56px;
    display: block;
  }

}

.header__menu {
  list-style: none;
  padding: 0;
  margin-top: 70px;
  margin-bottom: 25px;
  overflow: hidden;
}

.header__menu-item {
  float: left;
  margin-right: 9%;

  &:last-child {
    margin-right: 0;
  }

  a {
    font-size: 16px;
    line-height: 22px;
    color: #42E4FF;
    text-decoration: none;
    
    &:hover {
      text-decoration: underline;
    }
    
  }
  
}

@media (max-width: 1100px) {

  .header__img {
    width: 450px;
    left: 10px;
  }

  .header__wrap {
    padding-left: 500px;
  }

}

@media (max-width: 980px) {

  .header__wrap {
    padding-left: 15px;
  }

  .header__img {
    display: none;
  }

  .header__course {
    text-align: center;
  }

  .header__videocourse {
    margin: 0 auto;
  }

  .header__menu {
    text-align: center;
    display: inline-block;
    margin-top: 20px;
  }

  .header__menu-item {
    margin: 0 10px;
  }

}

@media (max-width: 480px) {

  .header__name {
    font-size: 26px;
    line-height: 35px;

    span {
      font-size: 35px;
      line-height: 41px;
    }

  }

  .header__menu-item {

    a {
      font-size: 14px;
      line-height: 18px;
    }

  }

}