.content {
  max-width: 970px;
  margin: 0 auto;
  background-color: $white;

  .content__bonus {

    .content__section {
      background: #2A59FF;

      .section__name {
        display: block;
        text-align: left;
        margin-top: 0;

        &::before {
          content: 'Бонус';
          counter-increment: item;
          font-weight: 700;
          font-size: 18px;
          line-height: 26px;
          display: block;
          width: 120px;
          border: 2px solid #FFD63F;
          padding-top: 3px;
          padding-bottom: 3px;
          color: $white;
          margin: 0 0 10px;
          text-align: center;
        }

      }

      .section__show {
        margin-top: 0;
      }

    }

    .section--bonus {
      background: #651FFF;

      @for $i from 1 through 10 {
        &:nth-of-type(#{$i}) {

          .section__name {

            &::before {
              content: 'Бонус #{$i}';
            }

          }

        }

      }

    }

  }

  .content__files {

    .content__section {
      background: none;


      .section__name {
        color: $black;
        font-size: 40px;
        line-height: 49px;

        &::before {
          background-color: transparent;
          content: '';
          display: none;
        }

      }

    }

  }

  .content__title {
    font-size: 47px;
    line-height: 57px;
    font-weight: 800;
    text-align: center;
    color: $black;
    font-family: 'Montserrat';
    margin-top: 80px;
    margin-bottom: 30px;
  }

}

@media (max-width: 980px) {

  .content {

    .content__title {
      font-size: 30px;
      line-height: 40px;
    }

    .content__files {

      .content__section {

        .section__name {
          font-size: 24px;
          line-height: 30px;
        }

      }

    }

  }

}

@media (max-width: 560px) {

  .content {

    .content__bonus {

      .content__section {

        .section__name {
          text-align: center;

          &::before {
            margin: 0 auto 10px;
          }

        }

      }

    }

  }

}